import React from 'react';
import Navigation from '../components/Navigation';

var state = true;

const Home = (props) => {
    let src = (props.isFirst && state) ? "media/cv.pdf" : "portfolio/media/cv.pdf";
    let src2 = (props.isFirst && state) ? "media/Offre.pdf" : "portfolio/media/Offre.pdf";
    state = false;
    return (
        <div className="home">
        <Navigation isFirst={props.isFirst}/>
        <div className="homeContent">
            <div className="content">
               <h1> Binnet BARRY</h1> 
               <h2> Business Analyst IT</h2>
               <div className="pdf">
                   <a href={src} target="_blank" rel="noopener noreferrer">Télécharger mon CV</a>
               </div>
               
               <div className="profil">
                    Détermination, persévérance et organisation font partie des qualités que j'ai développées durant mon parcours,  travailler sur des projets de développement très innovants reste ma forte motivation pour évoluer dans le domaine du numérique. Ainsi, avec mon sens d'adaptation et d'autonomie je pourrais facilement m’intégrer dans votre équipe et apporter mon plus à vos projets. Je suis un grand passionné par le monde de l'IT. 
                </div>
            </div>
        </div>
        </div>
    );
};

export default Home;